.order-list {
  list-style-type: none;
  padding: 0;
}

.order-item {
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.checked {
  background-color: #d3d3d3;
  text-decoration: line-through;
}

.unchecked {
  background-color: #fff;
}

.toggle-button {
  margin-bottom: 10px;
}
